.MainContainer{
    display: flex;
    flex-direction: column;

    height: fit-content;
}

.MainContentContainer{
    display: flex;

    flex-direction: column;

}

.MainContentContainer header{
    margin-top: 1rem;
    padding-bottom: 1rem;

    font-family: "PlayFair Display";
    font-weight: 200;
    font-size: 2.5rem;
    color: rgb(49, 47, 47);

    border-bottom: 1px solid rgb(177, 177, 177);
}

.EvaluateButton{
    background: linear-gradient(92.77deg, rgba(86, 213, 241, 0.79) 0%, rgba(14, 120, 219, 0.85) 100%);
    color: rgb(255, 252, 252);

    

    font-size: 2rem;
    font-family: "Roboto";
    font-weight: 100;

    width: 20%;
    max-width: 500px;
    min-width: 240px;

    margin-right: 1rem;

    box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.25);

    align-self: center;

    border-radius: 10px;
    border: none;

    height: 4rem;
}

.EvaluateButton:hover,.EvaluateButton:active{
    background: linear-gradient(92.77deg, rgba(66, 167, 190, 0.79) 0%, rgba(11, 99, 181, 0.85) 100%); 

    cursor: pointer;
}

.AgainButton{
    

    background:linear-gradient(92.53deg, #FC3838 3.34%, #FF8A00 97.64%) ;
    color: rgb(255, 252, 252);

    font-size: 2rem;
    font-family: "Roboto";
    font-weight: 100;

    width: 20%;
    max-width: 500px;
    min-width: 240px;

    margin-left: 1rem;
    align-self: center;

    box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.25);

    border-radius: 10px;
    border: none;

    height: 4rem;
}

.AgainButton:hover,.AgainButton:active{
    background: linear-gradient(92.53deg, #bc2323 3.34%, #c96e06 97.64%);

    cursor: pointer;
}

.ButtonsContainer{
    display: flex;
    width: 90%;

    align-self: center;
    
    justify-content: center;
    

    padding-top: 2rem;
    margin-top: 2rem;
    margin-bottom: 2rem;

    
}

@media screen and (max-width:600px){
    .ButtonsContainer{
        flex-direction: column;
        box-sizing: border-box;
    }

    .EvaluateButton{
        margin-right: 0px;
        margin-bottom: 2rem;
    }

    .AgainButton{
        margin: 0px;
    }
}

